:root {
    --primary: #2079c7;
    --standard-margin: 10px;
    --light-gray: #fafafa;
    --semi-light-gray: #dedede;
}
* {    
    margin: 0;
    padding: 0;
}
body {
    font-family: 'Poppins', sans-serif;
}
button {
    cursor: pointer;
}
.btn {
    font-size: 14px;
    padding: 6px 12px;
    margin-bottom: 0;
    display: inline-block;
    text-decoration: none;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    transition: background .5s ease-out;
}
.btn-primary {
    background-color: #2079c7;
    background-color: var(--primary);
    color: white;
}
.btn-primary:hover {
    background-color: #14558d !important;
}
.btn-accent {
    background-color: #fbb13c;
}
.btn-accent:hover {
    background-color: #fda81f !important;
}
.btn:hover {
    background-color: #fafafa;
    background-color: var(--light-gray);
}
.container {
    padding: 20px;
}
.flex {
    display: flex;
}
.flex-col-eq > div {
    flex: 1 1;
}
.fg-1 {
    grid-gap: 10px;
