}
.bb {
    box-sizing: border-box;
}
.circle_icon {
    display: flex;
    justify-content: center;
    height: 16px;
    width: 16px;
    border: none;
    outline: none;
    align-items: center;
    font-weight: bold;
    background-color: #2079c7;
    background-color: var(--primary);
    border-radius: 50%;
}
.text-input_sm {
    padding: 3px;
