}
.danger {
    background-color: red;
    color: white;
}

.light-text {
    font-weight: 300;
}
.select_list {
    list-style: none;
}
.select_list > li{
    padding: 5px 10px 5px 10px;
    border-radius: 5px;
    margin-bottom: 3px;
    cursor: pointer;
}
.select_list > li > .close_icon {
    display: none;
    height: 32px;
    width: 32px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 50%;
}
.select_list > li > .close_icon:hover {
    background-color: #dedede;
    background-color: var(--semi-light-gray);
}
.select_list > li:hover {
    background-color: #fafafa;
    background-color: var(--light-gray);
}
.select_list > li:hover .close_icon {
    display: flex;
    
}
.scroll-window {
    overflow-y: scroll;
}
.full-height {
    height: 100vh;
}
.pdfDownload {
    position: absolute;
    left: 0;
    top: 0;
    -webkit-transform-origin: top left;
            transform-origin: top left;
    -webkit-transform: scale(0.565);
            transform: scale(0.565);
    z-index: -1;
}

